
import { PropType, defineComponent } from 'vue'
import PDFPopup from '@/shared/components/PDFPopup/PDFPopup.vue'
export default defineComponent({
    name: "cib201001sp01",
    props: {
        formData: {
            type: [] as PropType<any>,
            default: [],
            required: true
        }
    },
    components: {
        PDFPopup
    },
    data() {
        return {
            servicePayload: '' as string,
            applicationFormRequest: '' as string,
            pdfConfig: {
                modalTitle: this.$t('cib201001sp01.preview_application_form_request'),
                fileName: this.$t('cib201001sp01.application_form_request'),
                paginate: true
            },
        }
    },
    mounted() {
        this.servicePayload = this.formData;
        this.renderPdfApplicationFormRequest();
    },
    methods: {
        async renderPdfApplicationFormRequest() {
            this.applicationFormRequest = await this.$file.previewImage(this.servicePayload);
        },
    }
})
