
import { PropType, defineComponent } from 'vue'
import PDFPopup from '@/shared/components/PDFPopup/PDFPopup.vue'
export default defineComponent({
    name: "cib201001sp02",
    props: {
        formData: {
            type: [] as PropType<any>,
            default: [],
            required: true
        }
    },
    components: {
        PDFPopup
    },
    data() {
        return {
            servicePayload: '' as string,
            applicationFormForCustomer: '' as string,
            pdfConfig: {
                modalTitle: this.$t('cib201001sp02.preview_application_form_for_customer'),
                fileName: this.$t('cib201001sp02.application_form_for_customer'),
                paginate: true
            },
        }
    },
    mounted() {
        this.servicePayload = this.formData;
        this.renderPdfCustomerFormApplication();
    },
    methods: {
        async renderPdfCustomerFormApplication() {
            this.applicationFormForCustomer = await this.$file.previewImage(this.servicePayload);
        },
    }
})
