
import { defineComponent } from "vue";
import CustomerInformation from "@/shared/components/CustomerInformation/CustomerInformation.vue";
import BreadCrumb from "@/shared/components/BreadCrumb/BreadCrumb.vue";
import Pagination from "@/shared/components/Pagination/Pagination.vue";
import { PAGE_SIZE } from "@/shared/constants/common.const";
import { IBSubscriptionList, CIB20100111_BODY, CIB20100111_RES } from "@/shared/type/CIB20100111.type";
import { CIB20100114_REQ_BODY, CIB20100114_RES, CustomerInfoInquiryDetail, } from "@/shared/type/CIB20100114.type";
import { CIB20100112_BODY, CIB20100112_RES } from '@/shared/type/CIB20100112.type'
import { CIB20100113_BODY, CIB20100113_RES } from '@/shared/type/CIB20100113.type'
import Cib201001sp01 from "../cib201001sp01/cib201001sp01.vue";
import Cib201001sp02 from "../cib201001sp02/cib201001sp02.vue";
import PopupService from "@/shared/services/PopupService";

const popup = new PopupService();

export default defineComponent({
    name: "cib201001sc00",
    components: {
        CustomerInformation,
        Pagination,
        BreadCrumb,
        Cib201001sp01,
        Cib201001sp02
    },

    data() {
        return {
            dataTable: [] as IBSubscriptionList[],
            searchFields: {
                customerNo: "",
                nationalID: "",
                passportNumber: "",
                businessNo: "",
                customerFullName: "",
            },
            customerInfo: {} as CustomerInfoInquiryDetail,
            isInValidInput: false,
            totalCount: 0,
            pageNumber: 1,
            pageSize: PAGE_SIZE,
            startingIndex: 1,
            isResetPage: false,
            userID: '' as string,
            isDisabledButtonApplication: true as boolean,
            applicationFormBankFileID: '' as string,
            applicationFormCustomerFileID: '' as string,
            isDisableRegister: true
        };
    },

    computed: {
        headers() {
            return [
                { columnName: this.$t("common.no") },
                { columnName: this.$t("common.action") },
                { columnName: this.$t("cib201001sc00.history") },
                { columnName: this.$t("cib201001sc00.ib_user_id") },
                { columnName: this.$t("cib201001sc00.customer_type") },
                { columnName: this.$t("cib201001sc00.status") },
                { columnName: this.$t("cib201001sc00.open_date") },
                { columnName: this.$t("cib201001sc00.close_date") },
            ];
        },

        covertCodeValue() {
            return (statusCode: string, statusValue: string) => `${statusCode}` + ' : ' + `${statusValue}`;
        }
    },
    watch: {
        userID(value) {
            value ? this.isDisabledButtonApplication = false : true;
        }
    },

    mounted() {
        if (this.$auth.getEnvMode() == "development" ? "5370606" : "") {
            if (this.searchFields.customerNo) {
                this.handleSearchCustomerInfo();
            }
        }
    },

    methods: {
        handlePagination(pageNumber: number) {
            this.pageNumber = pageNumber;
            this.isResetPage = false;
            this.handleSearchCustomerInfo();
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex;
        },

        handleResetSearch() {
            this.searchFields.customerNo = "";
            this.searchFields.nationalID = "";
            this.searchFields.passportNumber = "";
            this.searchFields.businessNo = "";
            this.searchFields.customerFullName = "";
            this.isInValidInput = false;
            this.pageNumber = 1;
            this.isResetPage = true;
            this.totalCount = 0;
            this.customerInfo = {} as CustomerInfoInquiryDetail;
            this.dataTable = [] as IBSubscriptionList[];
        },

        async getCustomerInformation() {
            const reqBody: CIB20100114_REQ_BODY = {
                customerNo: this.searchFields.customerNo
            };

            // ---- Adapter: CIB20100114_Adapter_CustomerInfo_InquiryDetail
            const resBody = (await this.$request.getRequest("CIB20100114", reqBody, false)) as CIB20100114_RES;
            if (resBody.header.result) {
                this.customerInfo = resBody.body;
            }
        },

        handleUserCreation() {
            this.$router.push({
                path: "CIB201002SC00",
                query: {
                    customerNo: this.searchFields.customerNo,
                },
            });
        },




        async handleSearchCustomerInfo(mode?: string) {
            if (!this.searchFields.customerNo) {
                return (this.isInValidInput = true);
            }

            const tableBody: CIB20100111_BODY = {
                customerNo: this.searchFields.customerNo,
                pageSize: this.pageSize,
                pageNumber: mode == 'search' ? 0 : this.pageNumber,
            };

            
            this.getCustomerInformation(); // call to get customer info
            const data = (await this.$request.getRequest("CIB20100111", tableBody, false)) as CIB20100111_RES;

            if (mode === 'search') {
                this.pageNumber = 1;
            }

            if (data.header.result) {
                this.isDisableRegister = false;
                this.dataTable = data.body.list.map((data: IBSubscriptionList) => {
                    return {
                        accountManagementTypeCode: data.accountManagementTypeCode,
                        allowAccountOpenCloseYN: data.allowAccountOpenCloseYN,
                        applicationDate: this.$datetime.formatStringToStringDate(data.applicationDate),
                        authorizationLineAssignTypeCode: data.authorizationLineAssignTypeCode,
                        authorizationLinePolicyTypeCode: data.authorizationLinePolicyTypeCode,
                        cellPhoneNo: data.cellPhoneNo,
                        cellPhoneNo2: data.cellPhoneNo2,
                        companyName: data.companyName,
                        customerCorpIndTypeCode: this.$codeConverter.convertValue("CustomerCorpIndTypeCode", data.customerCorpIndTypeCode),
                        customerName: data.customerName,
                        customerNo: data.customerNo,
                        email: data.email,
                        feeExemptYN: data.feeExemptYN,
                        feeServiceStatusCode: data.feeServiceStatusCode,
                        feeWithdrawalAccountNo: data.feeWithdrawalAccountNo,
                        languageCode: data.languageCode,
                        mainTransferAccount: data.mainTransferAccount,
                        mainUserRoleID: data.mainUserRoleID,
                        managerBankID: data.managerBankID,
                        mobileISDCode: data.mobileISDCode,
                        mobileISDCode2: data.mobileISDCode2,
                        securityMediaNo: data.securityMediaNo,
                        securityMediaTypeCode: this.$codeConverter.convertValue("SecurityMediaTypeCode", data.securityMediaTypeCode),
                        serviceStatusCode: this.covertCodeValue(data.serviceStatusCode, this.$codeConverter.convertValue("ServiceStatusCode", data.serviceStatusCode)),
                        subscriptionApprovalTypeCode: data.subscriptionApprovalTypeCode,
                        terminationDate: this.$datetime.formatStringToStringDate(data.terminationDate),
                        userID: data.userID,
                        userName: data.userName,
                    };
                });
                this.totalCount = data.body.totalCount;
            }
        },

        // resetForm() {
        //   for (const key in this.searchFields) {
        //     this.searchFields[key] = "";
        //   }
        //   // this.searchFields.customerNo = `${this.$auth.getEnvMode() == "development" ? "5370606" : ""}`,
        //   this.handleSearchCustomerInfo();
        //   this.isInValidInput = false;
        // },

        handleEditCustomerInfo(rowData: IBSubscriptionList) {
            this.$router.push({
                path: "/CIB201003SC00",
                params: { id: this.searchFields.customerNo },
                query: {
                    type: "edit",
                    customerNo: rowData.customerNo,
                    userID: rowData.userID,
                },
            });
        },

        handleGetCustomerHistoryInfo(userID: string, customerNo: string) {
            this.$router.push({
                path: `CIB202001SC00`,
                query: {
                    userID: userID,
                    customerNo: customerNo,
                },
            });
        },
        async getFormApplicationFormBankData() {
            const requestBody: CIB20100112_BODY = {
                customerNo: this.searchFields.customerNo,
                userID: this.userID
            }
            const responseBody = await this.$request.getRequest("CIB20100112", requestBody, false) as CIB20100112_RES;
            if (responseBody.header.result) {
                this.applicationFormBankFileID = responseBody.body.fileId;
            }

        },

        async getFormApplicationFormCustomer() {
            const requestBody: CIB20100113_BODY = {
                customerNo: this.searchFields.customerNo,
                userID: this.userID
            }
            const responseBody = await this.$request.getRequest("CIB20100113", requestBody, false) as CIB20100113_RES;
            if (responseBody.header.result) {
                this.applicationFormCustomerFileID = responseBody.body.fileId;
            }

        },

        async handleApplicationFormBank() {
            if (this.userID) {
                await this.getFormApplicationFormBankData();
                if (this.applicationFormBankFileID) {
                    popup.onOpen({
                        dataProp: {
                            formData: this.applicationFormBankFileID
                        },
                        component: Cib201001sp01,
                        callback: (v) => {
                            console.log(v);
                        }
                    })
                }

            }

        },

        async handleApplicationFormCustomer() {
            if (this.userID) {
                await this.getFormApplicationFormCustomer();
                if (this.applicationFormCustomerFileID) {
                    popup.onOpen({
                        dataProp: {
                            formData: this.applicationFormCustomerFileID
                        },
                        component: Cib201001sp02,
                        callback: (v) => {
                            console.log(v);
                        }
                    })
                }

            }

        },

        rowClick(item) {
            this.userID = item.userID;
        }


    },
});
